import React, { useState } from 'react';
import { Link } from 'react-router-dom';

import manifest from './data/navbar.json';


const RenderModal = manifest['Modal'].map( (manifest, index) => {

        return (
            <Link to={manifest.Link} key={index}><div className='font-normal text-base border border-gray-300 rounded-lg px-5 py-3'>{manifest.Option}</div></Link>
        )
    }
)

const Modal = () => {
    return (
        <section className='absolute animate__animated animate__bounce left-1/2 transform translate-y-20 w-3/4 h-2/3'>
            <div className='flex flex-col p-4 space-y-6 bg-white rounded-2xl shadow-xl'>
                <h3 className='font-semibold text-xl'>I am a...</h3>
                {RenderModal}
            </div>
        </section>
    )
}

const Render = () => {

    const [listOpen, setListOpen] = React.useState(false);
    const [modalOpen, setModalOpen] = React.useState(false);

    return (
        <nav className='flex flex-row items-center bg-white fixed top-0 w-full z-50 h-14 px-6 border border-gray-300'>
            <svg onClick={()=>{setListOpen(!listOpen); setModalOpen(false);}} className='w-6 mr-auto' fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2 6h16M2 12h16M2 18h16"/></svg>
            <Link to="/" className='absolute left-1/2 -ml-3.5'><img src={require('./images/scclogo.png').default} alt='scc logo' className='w-7' onClick={()=>{setListOpen(false); setModalOpen(false);}} /></Link>
            <button onClick={()=>{setListOpen(false); setModalOpen(!modalOpen);}}  className='ml-auto font-normal text-sm border border-gray-300 rounded-lg px-2.5 py-1.5'>Sign in</button>
            {modalOpen?<Modal />:null}
        </nav>
    )
}

export default Render
