import React from 'react'
import manifest from './data/student.json';


const RenderApp = manifest['Applications'].map( (manifest, index) => {

    return (
        <a href={manifest.Link} target="_blank" rel="noopener noreferrer" key={index} className='w-1/2 p-1'>
            <img src={require('./images/office365.jpg').default} alt='bg' className='w-full object-cover'/>
            <span className='flex bg-blue-600 px-2 text-white items-center py-2 w-full'>
                <h4 className='text-base font-normal'>{manifest.Name}</h4>
                <svg className="w-5 h-5 ml-auto" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 5l7 7-7 7M5 5l7 7-7 7" /></svg>
            </span>
        </a>
    )
  }
)

const RenderPA = manifest['Payment & Accounts'].map( (manifest, index) => {

    return (
        <a href={manifest.Link} key={index} className='w-full p-1'>
            <span className='flex bg-blue-600 px-2 text-white items-center py-2 w-full'>
                <h4 className='text-base font-normal'>{manifest.Name}</h4>
                <svg className="w-5 h-5 ml-auto" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 5l7 7-7 7M5 5l7 7-7 7" /></svg>
            </span>
        </a>
    )
  }
)

const Render = () => {
    return (
        <section className='min-h-screen w-full'>
            <img src={require('./images/bgstudents.jpg').default} alt='bg' className='w-full object-cover mt-16'/>
            <div className='w-full min-h-screen shadow-lg flex flex-col md:w-2/3 justify-start bg-white inset-y-96 p-10'>
                <h2 className='text-3xl font-normal text-black'>{manifest.Title}</h2>
                    <section className='flex flex-row flex-wrap pt-10'>
                        {RenderApp}
                    </section>
                    <section className='flex flex-row flex-wrap pt-10'>
                        {RenderPA}
                    </section>
            </div>
        </section>
    )
}

export default Render
